import { Component, OnInit } from '@angular/core';
import { Egreso } from '../egresos/egreso.model';
import { EgresoService } from '../egresos/egreso.service';
import { Ingreso } from '../ingresos/ingreso.model';
import { IngresoService } from '../ingresos/ingreso.service';

@Component({
  selector: 'app-formulario',
  templateUrl: './formulario.component.html',
  styleUrls: ['./formulario.component.sass']
})
export class FormularioComponent implements OnInit {
  descripcionInput: string;
  valorInput: number;
  opcionInput: string = "+";
  color: string = 'btn-success';
  inputText: string = 'inputTextGreen';

  constructor(private egresoService: EgresoService, 
              private ingresoService: IngresoService) {

  }

  ngOnInit(): void {
    
  }

  onAgregar(){
    if(this.opcionInput === '+'){
      let ingreso: Ingreso = new Ingreso(this.descripcionInput, this.valorInput);
      this.ingresoService.agregarIngreso(ingreso);
    }else if(this.opcionInput === '-'){
      let egreso: Egreso =  new Egreso(this.descripcionInput, this.valorInput);
      this.egresoService.agregarEgreso(egreso);
    }
  }
  tipoOperacion(event){
    this.opcionInput = event.target.value;
  }

}
