import { Ingreso } from "./ingreso.model";

export class IngresoService{
    ingresos: Ingreso[] = [new Ingreso('Venta de garaje', 500), 
                           new Ingreso('Bingo de recolecta', 400)];

    calcularIngresoTotal(ingresos:Ingreso[]): number{
        let totalIngreso: number;
        for (let i = 0; i < this.ingresos.length; i++){
            totalIngreso += this.ingresos[i].valor;
        }
        return totalIngreso;
    }

    agregarIngreso(ingreso: Ingreso){
        this.ingresos.push(ingreso);
    }

    eliminarIngreso(ingreso: Ingreso){
        const indice = this.ingresos.indexOf(ingreso);
        this.ingresos.splice(indice, 1);
    }
}